
  <template>
  <div class="my-card-list">
    <!--  -->
    <div v-if="list.length && !this.currentLoading" class="my-card-list__wrapper">
      <el-row v-bind="rowProps">
        <el-col v-for="(item, index) in list"
                :key="index"
                v-bind="colProps"
                :style="colStyle">
          <slot
            v-bind="{item, index, page:currentPage,pageSize:currentPageSize,total:currentTotal,columns:currentColumn}">
            {{item}}
          </slot>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <div v-if="this.currentLoading" class="my-card-list__loading">
      <slot name="loading">...</slot>
    </div>

    <!--  -->
    <div v-if="!this.currentLoading && !list.length" class="my-card-list__empty">
      <slot name="empty"></slot>
    </div>

    <!--  -->
    <div v-if="$slots.append" class="my-card-list__append">
      <slot name="append"></slot>
    </div>

    <!--  -->
    <Pagination v-if="pager && currentTotal"
                class="my-card-list__pager"
                ref="pager"
                v-bind="pagerProps"
                :total="currentTotal"
                :current-page="currentPage"
                @size-change="handlePageSizeChange"
                @current-change="handlePageChange"></Pagination>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Col as __Col__,Row as __Row__} from 'element-ui';
     
     
     
     
     /**
   * 
   * @module $ui/components/my-card-list
   */
  import responsive, {responsiveArray} from '$ui/utils/responsive'
  import {Pagination} from 'element-ui'
  // 
  const defaultPagerProps = {
    align: 'center',
    background: true,
    layout: 'prev, pager, next'
  }

  /**
   * 
   * @member slots
   * @property {string} default item index 
   * @property {string} empty 
   * @property {string} loading 
   * @property {string} append 
   */
  export default {
    name: 'MyCardList',
    components: {
      Pagination
    },
    /**
     * 
     * @member props
     * @property {Array} [data] 
     * @property {Function} [loader] loader  dataPromise {list, total}
     * @property {number|object} [columns]  {xxl,xl,lg,md,sm,xs}
     * @property {boolean|object} [pager] 
     * @property {number} [page=1] , 1
     * @property {number} [pageSize=12] 
     * @property {number} [total=0] 
     * @property {boolean} [auto] loader
     * @property {number} [gutter=12] 
     * @property {boolean} [loading] loading
     */
    props: {
      // 
      data: Array,
      // loader  dataPromise{list, total}
      loader: Function,
      //  {xxl,xl,lg,md,sm,xs}
      columns: {
        type: [Number, Object],
        default: 3,
        validator(val) {
          return typeof val === 'number' ? 24 % val === 0 : true
        }
      },
      // , 1
      page: {
        type: Number,
        default: 1
      },

      // 
      pageSize: {
        type: Number,
        default: 12
      },

      // 
      total: {
        type: Number,
        default: 0
      },

      // 
      pager: {
        type: [Boolean, Object]
      },
      // loader
      auto: {
        type: Boolean,
        default: true
      },
      // 
      gutter: {
        type: Number,
        default: 12
      },
      loading: Boolean
    },
    data() {
      return {
        list: [],
        // 
        screens: {},
        currentColumn: 3,
        currentPage: this.page,
        currentTotal: this.total,
        currentPageSize: this.pageSize,
        currentLoading: this.loading
      }
    },
    computed: {
      rowProps() {
        return {
          gutter: this.gutter
        }
      },
      colProps() {
        return {
          span: Math.floor(24 / this.currentColumn)
        }
      },
      colStyle() {
        return {
          paddingBottom: `${this.gutter}px`
        }
      },
      pagerProps() {
        return {
          ...defaultPagerProps,
          ...this.pager,
          pageSize: this.pageSize,
          total: this.total,
          currentPage: this.page
        }
      }
    },
    watch: {
      data: {
        immediate: true,
        handler(val = []) {
          this.list = Object.freeze(val.slice(0))
        }
      },
      loading: {
        immediate: true,
        handler(val) {
          this.currentLoading = val
        }
      },
      columns: {
        immediate: true,
        handler() {
          this.setupResponsive()
          this.currentColumn = this.getResponsiveValue()
        }
      },
      screens() {
        this.currentColumn = this.getResponsiveValue()
      },
      pagerProps: {
        immediate: true,
        handler(props) {
          this.currentPage = props.currentPage
          this.currentTotal = props.total
          this.currentPageSize = props.pageSize
          this.auto && this.$nextTick(this.load)
        }
      }
    },
    methods: {
      // 
      setupResponsive() {
        this.token && responsive.off(this.token)

        // 
        if (typeof this.columns !== 'object') return

        this.token = responsive.on(screens => {
          this.screens = screens
        })
      },
      // 
      getResponsiveValue() {
        const columns = this.columns
        const defaultValue = 3
        if (!columns) return defaultValue
        // 
        if (typeof columns === 'object') {
          for (let i = 0; i < responsiveArray.length; i++) {
            const breakpoint = responsiveArray[i]
            if (this.screens[breakpoint]) {
              return columns[breakpoint] || defaultValue
            }
          }
        }
        // 
        return columns
      },
      handlePageChange(page) {
        this.currentPage = page
        /**
         * 
         * @event page-change
         * @param {number} page 
         * @param {number} pageSize 
         */
        this.$emit('page-change', page, this.currentPageSize)
        this.load()
      },
      handlePageSizeChange(size) {
        this.currentPage = 1;
        this.currentPageSize = size
        /**
         * 
         * @event size-change
         * @param {number} pageSize 
         */
        this.$emit('size-change', size)
        this.load()
      },
      load() {
        if (!this.loader) return

        this.currentLoading = true
        this.loader(this.currentPage, this.currentPageSize).then(res => {
          this.list = res.list
          this.currentTotal = res.total || 0
          this.$emit('success', res)
        }).catch(e => {
          this.$emit('error', e)
        }).finally(r => {
          this.currentLoading = false
        })

      },
      /**
       * 
       * @method refresh
       * @param {number} [page] 
       */
      refresh(page) {
        this.currentPage = page || this.currentPage
        this.load()
      }
    }
  }
     __vue__.use(__Col__);
__vue__.use(__Row__)
     
    
  </script>



  