<template>
  <div class="my-float" :class="classes">
    <slot></slot>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-float
   */

  /**
   * 
   * @member slots
   * @property {string} default 
   */

  export default {
    name: 'MyFloat',
    provide() {
      return {
        wrapper: this
      }
    },
    /**
     * 
     * @member props
     * @property {boolean} [reverse=false] 
     * @property {boolean} [fit=false] 100%
     */
    props: {
      // 
      reverse: {
        type: Boolean,
        default: false
      },
      // 100%
      fit: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      classes() {
        return {
          'my-float--fit': this.fit
        }
      }
    }
  }
</script>

